.first-todo-background {

    background: rgba(0, 0, 0, 0.507);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    height: 100vh;

}

.first-todo-message {
    font-size: 4rem;
    color: aquamarine;
    text-align: center;
}

.Icon-container--smile{
    margin:50px 50% 0 50%
    
}
.Icon-svg--smile{
    transform: scale(2.5); 
}